export default {
  props: ["img", "description"],
  data() {
    return {
      active: false,
    };
  },
  mounted() {},
  methods: {
    toggle() {
      this.active = !this.active;
    },
    toggleOutside(e) {
      if (e.target == e.currentTarget) this.active = !this.active;
    },
  },
};
