my-16 kb-max-w\" data-v-384646bf><h2 class=\"overline mb-8\" data-v-384646bf>Overview</h2> <div class=\"flex flex-col md:flex-row gap-8\" data-v-384646bf><h3 class=\"flex-1 heading\" data-v-384646bf>Scaling a team of new designers.</h3> <p class=\"flex-1\" data-v-384646bf>\n        This was my first time leading a team of 3 other designers, all coming\n        in with various skill levels. As the Hackademics team continued to\n        scale up, I wanted to produce something with lasting change for the\n        organization to increase efficiency and help maintain consistency\n        across platforms. To uphold Hackville's brand values and visual\n        direction, I created a brand and illustration guide for my team to\n        follow for this and future events.\n      </p></div></div> <div class=\"flex flex-col md:flex-row gap-16 items-center my-16\" data-v-384646bf><div class=\"flex-1\" data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleProblem.png")))+" alt class=\"kb-img-max-h object-contain\" data-v-384646bf></div> <div class=\"flex-1 p-4\" data-v-384646bf><h2 class=\"overline mb-4\" data-v-384646bf>The Problem</h2> <p class=\"subheading-2\" style=\"max-width: 620px\" data-v-384646bf>\n        How might we uphold brand consistency using visual language to make\n        our event appealing to students?\n      </p></div></div> <div class=\"p-4 kb-mw-800\" data-v-384646bf><div class=\"subheading mb-4\" data-v-384646bf>Competitive Analysis</div> <p data-v-384646bf>\n      When studying other hackathons, I found the quality of their branding to\n      vary significantly due to most organizers being students (many without\n      design backgrounds); many looking outdated. Well-established\n      institutions with long-standing hackathons typically had the highest\n      quality branding. Most opted for the flat or isometric illustration,\n      coloured with tech blues, purples, reds, and whites.\n    </p></div> <div class=\"my-16\" data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleCompetition.png")))+" alt data-v-384646bf> <p class=\"text-center grey mt-2\" data-v-384646bf>\n      People vector created by macrovector - www.freepik.com\n    </p></div> <div class=\"p-4 kb-max-w my-16\" data-v-384646bf><div class=\"subheading mb-4 text-center\" data-v-384646bf>Moodboard</div> <img"+(_vm._ssrAttr("src",require("assets/images/HackvilleIllustrationMoodboard.png")))+" alt data-v-384646bf> <p class=\"kb-mw-800 mt-8\" data-v-384646bf>\n      I was inspired by geometric, flat style for its clean lines,\n      replicability, and easily reusable within a component library. I liked\n      the idea of creating a theme around complex systems and machinery for\n      the designs as the challenge theme for the event was smart cities.\n    </p></div> <div class=\"p-4 kb-max-w\" data-v-384646bf><h2 class=\"overline mb-4\" data-v-384646bf>The Solution</h2> <div class=\"flex flex-col md:flex-row gap-8\" data-v-384646bf><div class=\"md:w-9/12\" data-v-384646bf><p class=\"heading\" data-v-384646bf>\n          A comprehensive brand guide and illustration guide.\n        </p></div> <div class=\"flex-1\" data-v-384646bf><p data-v-384646bf><strong data-v-384646bf>Deliverables</strong></p> <ul data-v-384646bf><li data-v-384646bf>Brand guide</li> <li data-v-384646bf>Illustration guide</li> <li data-v-384646bf>Website UI</li> <li data-v-384646bf>Event Assets</li></ul></div></div> <div class=\"flex flex-col md:flex-row items-center md:justify-between gap-8 my-16\" data-v-384646bf><div class=\"w-8/12 text-center\" data-v-384646bf><div class=\"subheading mb-4 text-center\" data-v-384646bf>Wordmark</div> <img"+(_vm._ssrAttr("src",require("assets/images/HackvilleWordmark.png")))+" alt data-v-384646bf></div> <div class=\"w-3/12 text-center\" data-v-384646bf><div class=\"subheading mb-4 text-center\" data-v-384646bf>Lettermark</div> <img"+(_vm._ssrAttr("src",require("assets/images/HackvilleLettermark.png")))+" alt data-v-384646bf></div></div> <p class=\"kb-mw-800 mt-8\" data-v-384646bf>\n      The previous lead had created the wordmark and lettermark here; however,\n      in the brand guide, I added spacing guides for each and rules on\n      applying the marks to various assets. The guide gave newer design team\n      members and team members from the other teams something to refer to\n      ensure the marks were used correctly.\n    </p> <div class=\"my-16\" data-v-384646bf><div class=\"subheading mb-4 text-center\" data-v-384646bf>Typography</div> <div class=\"flex items-center justify-center gap-8 md:gap-32 flex-col md:flex-row\" data-v-384646bf><div class=\"flex-1\" data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleTypographySpace.png")))+" alt class=\"w-full object-contain\" style=\"max-height: 300px\" data-v-384646bf></div> <div class=\"flex-1\" data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleTypographyKarla.png")))+" alt class=\"w-full object-contain\" style=\"max-height: 300px\" data-v-384646bf></div></div></div> <div class=\"my-16\" data-v-384646bf><div class=\"subheading mb-4 text-center\" data-v-384646bf>Colours</div> <div class=\"flex gap-4\" data-v-384646bf><div class=\"flex-1 text-white text-center py-32\" style=\"background: #00194b\" data-v-384646bf>\n          #00194B\n        </div> <div class=\"flex-1 text-white text-center py-32\" style=\"background: #2d65b5\" data-v-384646bf>\n          #2D65B5\n        </div> <div class=\"flex-1 text-white text-center py-32\" style=\"background: #5d92d1\" data-v-384646bf>\n          #5D92D1\n        </div> <div class=\"flex-1 text-grey-400 text-center py-32\" style=\"background: #cddcf1\" data-v-384646bf>\n          #CDDCF1\n        </div></div> <div class=\"flex gap-4 mt-4 flex-col md:flex-row\" data-v-384646bf><div class=\"flex-1 text-grey-400 text-center py-6\" style=\"background: #ffffff\" data-v-384646bf>\n          #FFFFFF\n        </div> <div class=\"flex-1 text-white text-center py-6\" style=\"background: #141414\" data-v-384646bf>\n          #141414\n        </div></div></div></div> <div class=\"mt-16 flex flex-col md:flex-row items-stretch\" data-v-384646bf><div class=\"md:w-7/12 px-16 py-40 flex flex-col justify-center items-end\" style=\"background-color: #cddcf1\" data-v-384646bf><div class=\"max-w-2xl p-4 max-h-80\" data-v-384646bf><h3 class=\"subheading mb-4\" data-v-384646bf>Illustration Guide</h3> <p data-v-384646bf>\n          I added three fundamental illustrative elements for my team to\n          follow within the guide: the shapes, highlights and outlines, and\n          the grid. To maintain the geometric style and elicit a blueprint\n          essence, designers created on top of a grid using seven basic shapes\n          to build out the illustrations. Designers could add highlights or\n          outlines using a single stroke to add detail to the shapes.\n        </p></div></div> <div class=\"md:w-5/12 px-16 py-40 flex flex-col justify-center\" style=\"background-color: #041a48\" data-v-384646bf><div class=\"max-w-lg p-4 md:max-h-80\" data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleIllustrationStroke.png")))+" alt class=\"mx-auto h-full w-auto\" data-v-384646bf></div></div></div> <div data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleIllustrationGrid.png")))+" alt class=\"kb-img-max-h md:kb-max-w\" data-v-384646bf></div> <div class=\"mb-16 text-center py-40 px-8\" style=\"background-color: #cddcf1\" data-v-384646bf><div class=\"kb-max-w\" data-v-384646bf><h3 class=\"subheading mb-8\" data-v-384646bf>Illustration Guide</h3> <img"+(_vm._ssrAttr("src",require("assets/images/HackvilleIllustrationShapes.png")))+" alt class=\"kb-img-max-h\" data-v-384646bf></div></div> <div class=\"my-16 text-center py-40 px-8\" data-v-384646bf><div class=\"kb-max-w\" data-v-384646bf><h3 class=\"subheading mb-8\" data-v-384646bf>Illustration Examples</h3> <img"+(_vm._ssrAttr("src",require("assets/images/HackvilleIllustrationExamples.png")))+" alt class=\"kb-img-max-h\" data-v-384646bf></div></div> <div class=\"my-16 text-center py-40 px-8\" data-v-384646bf><div class=\"kb-max-w\" data-v-384646bf><h3 class=\"subheading mb-8\" data-v-384646bf>Event Assets</h3> <div class=\"flex kb-max-w gap-4\" data-v-384646bf><div data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleEventAssets-1.png")))+" alt class=\"kb-img-max-h\" data-v-384646bf> <p class=\"mt-2\" data-v-384646bf>Registration Site</p></div> <div data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleEventAssets-2.png")))+" alt class=\"kb-img-max-h\" data-v-384646bf> <p class=\"mt-2\" data-v-384646bf>Event banner</p></div></div> <div class=\"kb-max-w text-center my-8\" data-v-384646bf><img"+(_vm._ssrAttr("src",require("assets/images/HackvilleScheduleMockup.png")))+" alt class=\"kb-img-max-h\" data-v-384646bf> <p class=\"mt-2\" data-v-384646bf>Display schedule</p></div></div></div> <div class=\"my-16 p-4 kb-max-w\" data-v-384646bf><h2 class=\"overline mb-8\" data-v-384646bf>Reflection</h2> <h3 class=\"heading\" data-v-384646bf>\n      Ideas are always stronger when backed by a good story.\n    </h3> <p class=\"mt-8\" data-v-384646bf>\n      This project taught me about leadership, managing a team, and setting\n      them up for success. I felt it was vital that I aligned all members on\n      the vision in a way that was communicated clearly and comprehensibly to\n      give the tasks a greater sense of purpose.\n    </p> <p class=\"mt-8\" data-v-384646bf>\n      If I were to do a project like this again, I think creating\n      brainstorming workshops for the team to inspire collaboration and give\n      everyone a chance to get to know each other better.\n    </p> <p class=\"mt-8\" data-v-384646bf>\n      I wish I had focused more on the target audience and their needs and\n      goals at the beginning of the project to help me create a stronger story\n      within the creative direction and visual design. At the time, the\n      direction was just a cool idea, but it lacked substance and cohesion.\n      Spending less time obsessing over perfecting the guides and allowing\n      myself and the team to get creating faster would've been a better\n      decision in this case.\n    </p></div>")])}
